document.addEventListener("DOMContentLoaded", () => {
  // desktop language switcher
  const desktopLangSwitcher = document.querySelector(".desktopHeader .language-switcher");
  const desktopLanguageList = document.querySelector(".desktopHeader .language-list");
  const desktopCurrentIcon = document.querySelector(".desktopHeader .current-language-icon img");
  const desktopCurrentLang = document.querySelector(".desktopHeader .current-language-name");

  //mobile language switcher
  const mobileLangSwitcher = document.querySelector(".mobileHeader .language-switcher");
  const mobileLanguageList = document.querySelector(".mobileHeader .language-list");
  const mobileCurrentIcon = document.querySelector(".mobileHeader .current-language-icon img");
  const mobileCurrentLang = document.querySelector(".mobileHeader .current-language-name");

  //footer language switcher
  const footerLangSwitcher = document.querySelector(".footer-language-switcher");
  const footerLanguageList = document.querySelector(".footer-language-list");
  const footerCurrentIcon = document.querySelector(".footer-current-language-icon img");
  const footerCurrentLang = document.querySelector(".footer-current-language-name");

  const allTextFieldsOnPage = document.querySelectorAll("[data-localization]");

  //getting cuurent file name (e.g. index.html) and removing extension
  const currentFile = !!window.location.pathname.split("/").pop()
    ? window.location.pathname.split("/").pop().split(".")[0] // remove .html extension
    : "index";

  async function changeTextLanguage(langCode) {
    try {
      const fetchedText = await fetch(`/locales/${langCode.toLowerCase()}/${currentFile}.json`);

      const textData = await fetchedText.json();

      allTextFieldsOnPage.forEach((el) => {
        const splitted = el.getAttribute("data-localization").split("-");
        if (splitted.length === 1) {
          el.textContent = textData[splitted[0]];
          return;
        }

        if (textData[splitted[0]] && textData[splitted[0]][splitted[1]]) {
          el.innerHTML = textData[splitted[0]][splitted[1]];
        }
      });
    } catch (err) {}
  }

  function createLi(list, lang) {
    const li = document.createElement("li");

    li.innerHTML = `<div class="language-list-icon"><img src="${lang.icon}" alt="Language flag"></div><span>${lang.name}</span>`;

    li.addEventListener("click", function () {
      desktopCurrentIcon.src = lang.icon;
      desktopCurrentLang.textContent = lang.code;
      mobileCurrentIcon.src = lang.icon;
      mobileCurrentLang.textContent = lang.code;
      footerCurrentIcon.src = lang.icon;
      footerCurrentLang.textContent = lang.name;

      list.classList.add("hidden-lang-list");

      changeTextLanguage(lang.code);
    });

    return li;
  }

  function fillLanguageLists() {
    //getting languages list and filling it on a page
    fetch("/locales/languages.json")
      .then((response) => response.json())
      .then((data) => {
        data.languages.forEach((lang) => {
          desktopLanguageList.appendChild(createLi(desktopLanguageList, lang));
          mobileLanguageList.appendChild(createLi(mobileLanguageList, lang));
          footerLanguageList.appendChild(createLi(footerLanguageList, lang));
        });
      });
  }

  fillLanguageLists();

  // allHeaderLangSwitchers.forEach((switcher) => {
  //   switcher.addEventListener("mouseover", (e) => {
  //     allLanguageLists.forEach((list) => {
  //       list.classList.remove("hidden-lang-list");
  //     });
  //   });

  //   switcher.addEventListener("mouseleave", (e) => {
  //     allLanguageLists.forEach((list) => {
  //       list.classList.add("hidden-lang-list");
  //     });
  //   });
  // });

  function toggleDropdown(e, list) {
    if (!list.contains(e.target)) {
      list.classList.toggle("hidden-lang-list");
    }
  }

  desktopLangSwitcher.addEventListener("click", (e) => {
    toggleDropdown(e, desktopLanguageList);
  });

  mobileLangSwitcher.addEventListener("click", (e) => {
    toggleDropdown(e, mobileLanguageList);
  });

  footerLangSwitcher.addEventListener("click", (e) => {
    toggleDropdown(e, footerLanguageList);
  });

  function onClickOutside(e, switcher, list) {
    if (!switcher.contains(e.target) && !list.classList.contains("hidden-lang-list")) {
      list.classList.add("hidden-lang-list");
    }
  }

  document.addEventListener("click", (e) => {
    onClickOutside(e, desktopLangSwitcher, desktopLanguageList);
    onClickOutside(e, mobileLangSwitcher, mobileLanguageList);
    onClickOutside(e, footerLangSwitcher, footerLanguageList);
  });
});
